export default {
	data() {
		return {
			userDto: {
				name: '',
				password: ''
			},
			verifyCode: '',
			r_password: ''
		};
	},
	onLoad() {},
	methods: {
		getVerifyCode: function() {
			uni.request({
				url: this.$baseUrl + '/sms?mobile=' + this.userDto.name,
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					mobile: this.userDto.name
				},
				success: res => {
					if (res.data.code === 1) {
						uni.showToast({
							title: '验证码发送成功'
						});
						(code = res.data.data), console.log(code), (mymobile = this.userDto.name);
					} else {
						uni.showModal({
							title: '验证码发送失败'
						});
					}
				}
			});
		},
		froget: function(userDto) {
			uni.request({
				url: this.$baseUrl + '/user/UserPassword',
				method: 'PUT',
				header: {
					'content-type': 'application/json'
				},
				data: {
					name: this.userDto.name,
					password: this.userDto.password,
					verifyCode: this.verifyCode
				},
				success: res => {
					if (res.data.code === 1) {
						uni.showToast({
							title: res.data.data
						});
					}
				}
			});
		},
		checkPhone(){
			var phone =this.userDto.name;
			    if(!(/^1[3456789]\d{9}$/.test(phone))){
					this.mobileTip ='手机格式不正确';
					this.status = true;
			        console.log('aaa')
			        return false;
			    } else{
					this.mobileTip ='';
					this.status = false;
				}
		}
	}
};
